<template>
  <div class="app-container">
    <el-row :gutter="20">
      <el-col :xs="24">
        <el-form :model="queryParams" ref="queryForm" :inline="true" v-show="showSearch" label-width="68px">
          <el-form-item label="日期范围">
            <el-date-picker
              v-model="dateRange"
              size="small"
              style="width: 240px"
              value-format="yyyy-MM-dd"
              type="daterange"
              range-separator="-"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
            <el-button icon="el-icon-refresh" size="mini" @click="resetQuery">重置</el-button>
          </el-form-item>
        </el-form>
          <el-col :xs="24" :sm="24" :md="12" :lg="8">
            <el-card class="update-log">
              <div slot="header" class="clearfix">
                <span>成本统计</span>
              </div>
              <div id="costEchart" style="height:500px;"></div>
            </el-card>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="8">
            <el-card class="update-log">
              <div slot="header" class="clearfix">
                <span>销售统计</span>
              </div>
              <div id="saleEchart" style="height:500px;"></div>
            </el-card>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="8">
            <el-card class="update-log">
              <div slot="header" class="clearfix">
                <span>利润统计</span>
              </div>
              <div id="profitEchart" style="height:500px;"></div>
            </el-card>
          </el-col>
      </el-col>
    </el-row>


  </div>
</template>

<script>
  export default {
    name: 'Profit',
    data () {
      return {
        // 显示搜索条件
        showSearch: true,
        // 日期范围
        dateRange: [],
        // 查询参数
        queryParams: {
          pageNum: 1,
          pageSize: 10,
          name: undefined,
          buyTime: undefined,

        },
      }
    },

    mounted () {
      this.statisticsCostData();

      this.statisticsSaleData();
    },

    methods: {
      statisticsSaleData () {
        // 基于准备好的dom，初始化echarts实例
        let myChart = echarts.init(document.getElementById('saleEchart'));

        // 指定图表的配置项和数据
        let option = {
          title: {
            text: 'ECharts 入门示例'
          },
          tooltip: {},
          legend: {
            data: ['销量']
          },
          xAxis: {
            data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
          },
          yAxis: {},
          series: [
            {
              name: '销量',
              type: 'bar',
              data: [5, 20, 36, 10, 10, 20]
            }
          ]
        };

        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
      },

      statisticsCostData(){
        // 基于准备好的dom，初始化echarts实例
        let myChart = echarts.init(document.getElementById('costEchart'));

        let option = {
          title: {
            text: 'Weather Statistics',
            subtext: 'Fake Data',
            left: 'center'
          },
          legend: {
            bottom: 10,
            left: 'center',
            data: ['CityA', 'CityB', 'CityD', 'CityC', 'CityE']
          },
          series: [
            {
              type: 'pie',
              radius: '65%',
              center: ['50%', '50%'],
              selectedMode: 'single',
              data: [
                {
                  value: 1548,
                  name: 'CityE',
                },
                { value: 735, name: 'CityC' },
                { value: 510, name: 'CityD' },
                { value: 434, name: 'CityB' },
                { value: 335, name: 'CityA' }
              ],
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                }
              }
            }
          ]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
      }
    }
  }
</script>

<style scoped>

</style>



